header {
  background: #2a2a2a;
  padding: 16px 32px;
  display: flex;
  color: white;
  align-items: center;
  box-shadow: 0 1px 8px 0 rgba(255, 255, 255, 0.2);
}

nav {
  margin-top: 8px;
  margin-bottom: -8px;
}

nav a {
  padding: 0 24px;
  display: inline-block;
  text-align: center;
  font-size: 24px;
}

nav hr {
  height: 2px;
  background: white;
  border: none;
  margin-top: 16px;
  width: 50%;
  text-align: left;

  /* Slide between active pages */
  --hrSlide: 0.4s ease-in-out;
  transition: var(--hrSlide);
  -o-transition: var(--hrSlide);
  -ms-transition: var(--hrSlide);
  -moz-transition: var(--hrSlide);
  -webkit-transition: var(--hrSlide);
}

@media only screen and (min-width: 1200px) {
  .nav-icon {
    flex: 1;
  }
}

@media only screen and (max-width: 500px) {
  .header-logo {
    font-size: 6vmin;
  }
}
